import { useState, useEffect } from 'react';
import axios from '../../lib/axios';

interface User {
  id: string;
  email: string;
  name: string;
  role: 'student' | 'teacher' | 'parent' | 'admin';
  phone?: string;
  createdAt: string;
  student?: {
    id: string;
    grade: number;
    schoolName?: string;
    parentPhone?: string;
  };
}

interface EditUserData {
  name: string;
  email: string;
  phone?: string;
  grade?: number;
  schoolName?: string;
  parentPhone?: string;
}

export default function UserManagement() {
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [editData, setEditData] = useState<EditUserData>({
    name: '',
    email: '',
    phone: '',
  });
  const [filterRole, setFilterRole] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    try {
      setLoading(true);
      const response = await axios.get('/api/v1/admin/users');
      setUsers(response.data.data || []);
    } catch (error) {
      console.error('   :', error);
      alert('   .');
    } finally {
      setLoading(false);
    }
  };

  const handleEditClick = (user: User) => {
    setEditingUser(user);
    setEditData({
      name: user.name,
      email: user.email,
      phone: user.phone || '',
      grade: user.student?.grade,
      schoolName: user.student?.schoolName || '',
      parentPhone: user.student?.parentPhone || '',
    });
  };

  const handleSaveEdit = async () => {
    if (!editingUser) return;

    try {
      await axios.put(`/api/v1/admin/users/${editingUser.id}`, editData);
      alert('  .');
      setEditingUser(null);
      fetchUsers();
    } catch (error: any) {
      console.error('   :', error);
      alert(error.response?.data?.message || '   .');
    }
  };

  const handleDeleteUser = async (userId: string) => {
    if (!confirm('   ?')) return;

    try {
      await axios.delete(`/api/v1/admin/users/${userId}`);
      alert(' .');
      fetchUsers();
    } catch (error: any) {
      console.error('  :', error);
      alert(error.response?.data?.message || '  .');
    }
  };

  const getRoleLabel = (role: string) => {
    const labels: Record<string, string> = {
      student: '',
      teacher: '',
      parent: '',
      admin: '',
    };
    return labels[role] || role;
  };

  const filteredUsers = users.filter((user) => {
    const matchesRole = filterRole === 'all' || user.role === filterRole;
    const matchesSearch =
      user.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      user.email.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesRole && matchesSearch;
  });

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-foreground">  </h1>
        <p className="text-muted-foreground mt-2">    .</p>
      </div>

      {/* Filters */}
      <div className="bg-card rounded-lg shadow p-4 border border-border">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-card-foreground mb-2"> </label>
            <select
              value={filterRole}
              onChange={(e) => setFilterRole(e.target.value)}
              className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
            >
              <option value="all"></option>
              <option value="student"></option>
              <option value="teacher"></option>
              <option value="parent"></option>
              <option value="admin"></option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-card-foreground mb-2"></label>
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="   "
              className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
            />
          </div>
        </div>
      </div>

      {/* User Count */}
      <div className="text-sm text-muted-foreground">
         {filteredUsers.length} 
      </div>

      {/* Users Table */}
      <div className="bg-card rounded-lg shadow overflow-hidden border border-border">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-border">
            <thead className="bg-muted">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                   
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-muted-foreground uppercase tracking-wider">
                  
                </th>
              </tr>
            </thead>
            <tbody className="bg-card divide-y divide-border">
              {filteredUsers.map((user) => (
                <tr key={user.id} className="hover:bg-muted/50 transition-colors">
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-foreground">{user.name}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-muted-foreground">{user.email}</div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-primary/10 text-primary">
                      {getRoleLabel(user.role)}
                    </span>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-muted-foreground">
                    {user.phone || '-'}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-muted-foreground">
                    {user.student ? (
                      <div className="space-y-1">
                        <div>: {user.student.grade}</div>
                        {user.student.schoolName && <div>: {user.student.schoolName}</div>}
                        {user.student.parentPhone && <div>: {user.student.parentPhone}</div>}
                      </div>
                    ) : (
                      '-'
                    )}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-muted-foreground">
                    {new Date(user.createdAt).toLocaleDateString('ko-KR')}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium space-x-2">
                    <button
                      onClick={() => handleEditClick(user)}
                      className="text-primary hover:text-primary/80 transition-colors"
                    >
                      
                    </button>
                    <button
                      onClick={() => handleDeleteUser(user.id)}
                      className="text-destructive hover:text-destructive/80 transition-colors"
                    >
                      
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Edit Modal */}
      {editingUser && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-card rounded-lg shadow-xl p-6 max-w-md w-full border border-border">
            <h2 className="text-xl font-bold text-card-foreground mb-4">  </h2>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-card-foreground mb-1"></label>
                <input
                  type="text"
                  value={editData.name}
                  onChange={(e) => setEditData({ ...editData, name: e.target.value })}
                  className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-card-foreground mb-1"></label>
                <input
                  type="email"
                  value={editData.email}
                  onChange={(e) => setEditData({ ...editData, email: e.target.value })}
                  className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-card-foreground mb-1"></label>
                <input
                  type="tel"
                  value={editData.phone}
                  onChange={(e) => setEditData({ ...editData, phone: e.target.value })}
                  className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                  placeholder="010-1234-5678"
                />
              </div>

              {editingUser.role === 'student' && (
                <>
                  <div>
                    <label className="block text-sm font-medium text-card-foreground mb-1"></label>
                    <select
                      value={editData.grade}
                      onChange={(e) => setEditData({ ...editData, grade: parseInt(e.target.value) })}
                      className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                    >
                      {[1, 2, 3, 4, 5, 6].map((grade) => (
                        <option key={grade} value={grade}>
                           {grade}
                        </option>
                      ))}
                      {[7, 8, 9].map((grade) => (
                        <option key={grade} value={grade}>
                           {grade - 6}
                        </option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-card-foreground mb-1"></label>
                    <input
                      type="text"
                      value={editData.schoolName}
                      onChange={(e) => setEditData({ ...editData, schoolName: e.target.value })}
                      className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                      placeholder=": "
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-card-foreground mb-1">
                        
                    </label>
                    <input
                      type="tel"
                      value={editData.parentPhone}
                      onChange={(e) => setEditData({ ...editData, parentPhone: e.target.value })}
                      className="w-full px-4 py-2 border border-border rounded-md focus:outline-none focus:ring-2 focus:ring-primary bg-input text-foreground"
                      placeholder="010-1234-5678"
                    />
                  </div>
                </>
              )}
            </div>

            <div className="flex justify-end gap-3 mt-6">
              <button
                onClick={() => setEditingUser(null)}
                className="px-4 py-2 border border-border rounded-md hover:bg-muted transition-colors text-foreground"
              >
                
              </button>
              <button
                onClick={handleSaveEdit}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
