import { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuthStore } from '../../stores/authStore';
import axios from '../../lib/axios';

interface TestTemplate {
  id: string;
  templateCode: string;
  grade: number;
  title: string;
  totalQuestions: number;
  timeLimit: number;
}

export default function TestStart() {
  const { templateCode } = useParams<{ templateCode: string }>();
  const navigate = useNavigate();
  const { user } = useAuthStore();
  const [template, setTemplate] = useState<TestTemplate | null>(null);
  const [loading, setLoading] = useState(true);
  const [starting, setStarting] = useState(false);

  useEffect(() => {
    fetchTemplate();
  }, [templateCode]);

  const fetchTemplate = async () => {
    try {
      setLoading(true);
      const response = await axios.get(`/api/v1/templates/${templateCode}`);
      setTemplate(response.data.data);
    } catch (error) {
      console.error('  :', error);
      alert('    .');
      navigate('/dashboard');
    } finally {
      setLoading(false);
    }
  };

  const handleStart = async () => {
    if (!template) return;

    try {
      setStarting(true);
      const response = await axios.post('/api/v1/sessions/start', {
        templateId: template.id,
      });

      const sessionId = response.data.data.sessionId;
      //    (     )
      navigate(`/test/survey/${sessionId}`);
    } catch (error: any) {
      console.error('  :', error);
      alert(error.response?.data?.message || '   .');
    } finally {
      setStarting(false);
    }
  };

  const getGradeName = (grade: number) => {
    if (grade <= 6) return ` ${grade}`;
    return ` ${grade - 6}`;
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-xl text-muted-foreground"> ...</div>
      </div>
    );
  }

  if (!template) {
    return null;
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="max-w-4xl mx-auto px-4 py-12">
        <div className="bg-card rounded-lg shadow-lg p-8 border border-border">
          {/*  */}
          <div className="text-center mb-8">
            <h1 className="text-3xl font-bold text-primary mb-2">
              {template.title}
            </h1>
            <p className="text-lg text-muted-foreground">
              {getGradeName(template.grade)}
            </p>
          </div>

          {/*   */}
          <div className="bg-muted rounded-lg p-6 mb-8">
            <h2 className="text-xl font-semibold mb-4 text-foreground">
               
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
                  <span className="text-2xl"></span>
                </div>
                <div>
                  <div className="text-sm text-muted-foreground"> </div>
                  <div className="text-lg font-semibold text-foreground">
                    {template.totalQuestions}
                  </div>
                </div>
              </div>

              <div className="flex items-center gap-3">
                <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
                  <span className="text-2xl"></span>
                </div>
                <div>
                  <div className="text-sm text-muted-foreground"> </div>
                  <div className="text-lg font-semibold text-foreground">
                    {template.timeLimit}
                  </div>
                </div>
              </div>

              <div className="flex items-center gap-3">
                <div className="w-12 h-12 bg-primary/10 rounded-full flex items-center justify-center">
                  <span className="text-2xl"></span>
                </div>
                <div>
                  <div className="text-sm text-muted-foreground"> </div>
                  <div className="text-lg font-semibold text-foreground">4 </div>
                </div>
              </div>
            </div>
          </div>

          {/*   */}
          <div className="bg-accent/50 rounded-lg p-6 mb-8 border border-accent">
            <h2 className="text-xl font-semibold mb-4 text-accent-foreground">
                 
            </h2>
            <ul className="space-y-3 text-accent-foreground">
              <li className="flex items-start gap-2">
                <span className="text-primary mt-1"></span>
                <span>
                    <strong>{template.timeLimit}</strong>  .
                </span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary mt-1"></span>
                <span>
                  <strong>{template.totalQuestions}</strong>  .
                </span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary mt-1"></span>
                <span>
                   , , ,       .
                </span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary mt-1"></span>
                <span>
                     <strong>  </strong>,     .
                </span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-primary mt-1"></span>
                <span>
                     .     .
                </span>
              </li>
            </ul>
          </div>

          {/*    */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
            <div className="bg-card border border-border rounded-lg p-4">
              <h3 className="font-semibold text-primary mb-2">  </h3>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li>  (  )</li>
                <li>  (  )</li>
                <li> / (  )</li>
                <li> / ( )</li>
              </ul>
            </div>

            <div className="bg-card border border-border rounded-lg p-4">
              <h3 className="font-semibold text-primary mb-2">   </h3>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li> / </li>
                <li>   </li>
                <li>   </li>
                <li>   </li>
              </ul>
            </div>
          </div>

          {/*   */}
          <div className="bg-muted rounded-lg p-4 mb-8">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-sm text-muted-foreground"></div>
                <div className="font-semibold text-foreground">{user?.name}</div>
              </div>
              <div>
                <div className="text-sm text-muted-foreground"></div>
                <div className="font-semibold text-foreground">{user?.email}</div>
              </div>
            </div>
          </div>

          {/*   */}
          <div className="flex gap-4">
            <button
              onClick={() => navigate('/dashboard')}
              className="flex-1 border border-border py-3 px-6 rounded-lg hover:bg-muted transition-colors text-foreground font-medium"
            >
              
            </button>
            <button
              onClick={handleStart}
              disabled={starting}
              className="flex-1 bg-primary text-primary-foreground py-3 px-6 rounded-lg hover:bg-primary/90 transition-colors disabled:opacity-50 disabled:cursor-not-allowed font-bold"
            >
              {starting ? '  ...' : ' '}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
